import { CheckOutlined, WarningOutlined } from "@ant-design/icons";
import { Button, Image } from "antd";
import Search from "antd/lib/input/Search";
import React, { useState } from "react";

const ProductRequirement = ({ item, handleReport, table, userId }) => {
    const [statusReport, setstatusReport] = useState(false);
    return (
        <div>
            {item.quantity > 0 && (
                <div className="product_item">
                    {item.image ? (
                        <Image
                            src={`http://${item.image}`}
                            width={65}
                            height={65}
                            preview={false}
                        />
                    ) : (
                        <Image
                            className="procuct__image__requirement"
                            src="error"
                            fallback="data:image/png;base64,"
                        />
                    )}
                    <span className="product_item__name">{item.name}</span>
                    {item.note && (
                        <span className="product_item__note">{`Ghi chú: ${item.note}`}</span>
                    )}

                    {!statusReport && (
                        <Button
                            icon={<WarningOutlined />}
                            style={{ marginLeft: 80, color: "red" }}
                            onClick={() => setstatusReport(true)}
                        />
                    )}
                    {statusReport && (
                        <div style={{ width: 100, marginLeft: 20 }}>
                            <Search
                                enterButton={<CheckOutlined />}
                                onSearch={(event) => {
                                    setstatusReport(false);
                                    handleReport(event, item, userId, table);
                                }}
                                size="small"
                                autoFocus
                            />
                        </div>
                    )}

                    <span className="product_item__quantity">
                        Số lượng: {item.quantity}
                    </span>
                </div>
            )}
        </div>
    );
};

export default ProductRequirement;
