import { Table } from "antd";
import Column from "antd/lib/table/Column";
import React from "react";
import { formatMoney } from "../../utils/formatNumber";
import moment from "moment";

class ComodityPrint extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { listComodity } = this.props;
    return (
      <Table
        size={"small"}
        dataSource={listComodity}
        rowKey="_id"
        pagination={{
          total: listComodity.length,
          pageSize: listComodity.length,
          hideOnSinglePage: true,
        }}
      >
        <Column title="Tên hàng hoá" dataIndex="name" key="name" />
        <Column
          title="Đơn giá"
          dataIndex="price"
          key="price"
          render={(price) => <span>{`${formatMoney(price)} VNĐ`}</span>}
        />
        <Column
          title="Số lượng"
          key="quantity"
          render={(item) => {
            return item && item.unit && item.unit.unit !== null ? (
              <span>{Math.floor(item.unit.unit * item.quantity)}</span>
            ) : (
              <span>{item.quantity}</span>
            );
          }}
        />
        <Column
          title="Ngày nhập"
          key="updatedAt"
          dataIndex="updatedAt"
          render={(value) => {
            return <span>{moment(value).format("DD/MM/YYYY")}</span>;
          }}
        />
        <Column
          title="Đơn vị quy ước"
          dataIndex="unit"
          key="unit"
          render={(item) => {
            return item && item.unit !== null ? (
              <span>{item.unitMath}</span>
            ) : (
              <span>Chưa có</span>
            );
          }}
        />
        <Column
          title="Nhà cung cấp"
          dataIndex="bySupplier"
          key="unit"
          render={(item) => <span>{item.name}</span>}
        />
      </Table>
    );
  }
}

export default ComodityPrint;
